import React from 'react';
import { Switch, Route } from 'react-router-dom';
import { LoginPage, ForgotPassword, UserPage } from '../../modules';

const PageContent = (props) => {
  return (
    <div>
      <Switch>
        <Route path="https://sherjlok15.github.io/test-form/" exact component={props.logged_in ? UserPage : LoginPage}/>
        <Route path="https://sherjlok15.github.io/test-form/forgot-password" component={ForgotPassword}/>
      </Switch>
    </div>
  );
}

export default PageContent;
